.hero__section {
  padding-top: 50px;
  padding-bottom: 50px;
  // background-image: url(../images/hero/shadow/hero_shadow-tab.svg);
  // background-position: -74px 6px;
  // background-repeat: no-repeat;
  // box-shadow: -5px 21px 26px rgba(0, 0, 0, 0.04);
}

.hero__title {
  font-family: var(--main-font);
  font-weight: 800;
  font-size: 20px;
  line-height: 1.35;
  letter-spacing: 0.03em;
  color: var(--main-color);

  margin-bottom: 30px;
}
.hero__text {
  font-family: var(--main-font);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.57;
  letter-spacing: 0.08em;

  color: var(--main-color);
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}
.hero__text.pale {
  color: var(--secondary-color);
}
.hero__text.accent {
  color: var(--accent-color);
}
.hero__left-side {
  display: flex;
  justify-content: center;
}
.hero__right-side {
  margin-bottom: 30px;
}

@media screen and (min-width: 768px) {
  .hero__section {
    padding-top: 70px;
    padding-bottom: 64px;

    // background-position: bottom;
    // box-shadow: none;
  }
  .hero__left-side {
    width: 376px;
    height: 321px;

    position: relative;
  }
  .hero__left--foto {
    position: absolute;
    left: 5px;
    width: 100%;
  }
  .hero__side {
    display: flex;
  }
  .hero__title {
    font-size: 26px;
    line-height: 1.3;
    width: 332px;
  }
  .hero__text {
    margin-left: 0;
  }
}

@media screen and (min-width: 1366px) {
  .hero__section {
    padding-top: 125px;
    padding-bottom: 0;

    // background-image: url(../images/hero/shadow/hero_shadow.svg);
    // background-position: -23px -114px;
  }
  .hero__left-side {
    width: 842px;
    height: 718px;
  }
  .hero__left--foto {
    top: -100px;
    right: 90px;
    left: 0;
  }
  .hero__title {
    font-size: 58px;
    line-height: 1.29;
    width: 642px;
    margin-bottom: 24px;
  }
  .hero__text {
    font-size: 20px;
    line-height: 1.65;
    white-space: nowrap;
    text-align: left;
    margin-bottom: 40px;
  }
}
@media screen and (min-width: 1600px) {
  .hero__section {
    background-position: -23px -114px;
  }
}
