@media screen and(min-width:768px) {
  .container-title {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media screen and(min-width:1366px) {
    .container-title {
      display: none;
    }
  }
}

@media screen and(min-width:320px) and (max-width: 767px) {
  .features {
    position: relative;
    display: block;

    &::after {
      position: absolute;
      display: block;
      content: '';
      bottom: 0px;
      left: 0px;
      width: 100%;
      height: 240px;
      background-color: white;
      z-index: 1000;
    }
  }
}

.features {
  background-color: var(--main-color);

  @media screen and (min-width: 768px) {
    background: linear-gradient(
      to bottom,
      #111518 0%,
      #111518 54%,
      white 54%,
      white 100%
    );
    height: 365px;
    // max-width: 1680px;
    // margin: 0 auto;
    // padding-bottom: 130px;
  }
}

.features_title {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.35;
  letter-spacing: 0.03em;
  color: #ffffff;
  margin-bottom: 30px;
  max-width: 180px;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.3;
    max-width: 370px;
  }
}

.features_border {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.91;
  letter-spacing: 0.08em;
  color: var(--accent-color);
  margin-bottom: 10px;

  @media screen and (min-width: 1366px) {
    font-size: 20px;
    line-height: 1.65;
    margin-left: 0px;
  }
}

.features_text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: 0.08em;
  color: var(--main-color);
  // width: 216px;

  @media screen and (min-width: 1366px) {
    font-size: 20px;
    line-height: 1.2;
  }
}

.features_item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container-bcg .features_item {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container-features {
    position: relative;
    display: block;
    z-index: 1050;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
  }
}

.container-features {
  width: 280px;
  height: 660px;
  padding-top: 20px;
  padding-bottom: 15px;
  padding-right: 14px;
  padding-left: 30px;
  background-color: #fff;

  @media screen and(min-width:768px) {
    width: 688px;
    height: 394px;
    padding-top: 30px;
    padding-bottom: 25px;
    padding-right: 118px;
    padding-left: 98px;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
  }

  @media screen and(min-width:1366px) {
    width: 1314px;
    height: 398px;
    padding-top: 70px;
    padding-bottom: 70px;
    padding-right: 84px;
    padding-left: 82px;
    position: absolute;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
    // z-index: 100;
  }
}

@media screen and(min-width:768px) and (max-width: 1365px) {
  .container-full {
    padding-bottom: 50px;
  }
}

.features_list {
  width: 236px;
  // padding-bottom: 59px;

  @media screen and (min-width: 768px) {
    width: 472px;
    display: flex;
    flex-wrap: wrap;
  }

  @media screen and (min-width: 1366px) {
    width: 1148px;
    display: flex;
    justify-content: space-between;
    //    padding-bottom: 130px;
    //    flex-wrap: wrap;
  }
}

.rezultat {
  padding-bottom: 15px;
}

@media screen and (min-width: 768px) {
  .features_item {
    width: 236px;
  }
}

@media screen and (min-width: 1366px) {
  .features_item {
    width: 356px;
  }
}
