.program_title_semester{
    position: relative;
    display: inline-block;
    margin-bottom: 5px;

    @media screen and (min-width:1366px) {
        margin-bottom: 20px;
    }

    &:before{

        position: absolute;
        display: inline-block;
        content: '';
        top: 5px;
        left: -30px;
        width: 12px;
        height: 12px;
        border-radius: 100%;
        background-color: var(--accent-color);

        @media screen and (min-width:768px) {
            left: -68px;
        }

        @media screen and (min-width:1366px) {
        width: 17px;
        height: 17px;
        top: -73px;
        left: -0px;
        z-index: 2;
        }
    }

}

