.teacher__container {
  padding-top: 39px;
  padding-bottom: 50px;
}

.teacher__main-heading {
  margin-bottom: 20px;
  @include font(800, 20px, 1.3, 0.03);
}
.teacher__left-side {
  width: 243px;
  margin-bottom: 20px;
}
.teacher__photo {
  width: 243px;
  margin-bottom: 21px;
  margin-right: 0;
}

.teacher__name {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--secondary-color);

  @include font(800, 20px, 1.219, 0.08);
}

.teacher__achievements {
  margin-bottom: 43px;
}

.teacher__achievements-item {
  display: flex;
  position: relative;
  padding-left: 26px;

  &:not(:last-child) {
    margin-bottom: 25px;
  }
}

.teacher__achievements-description::before {
  content: '';
  background-image: url(../../src/images/bonfire.svg);
  width: 17px;
  height: 14px;
  position: absolute;
  left: 0px;
}

.teacher__achievements-description,
.teacher__education-description {
  @include font(500, 12px, 1.67, 0.06);
}

.teacher__education {
  margin-bottom: 10px;
  color: var(--secondary-color);
  position: relative;

  &::before {
    position: absolute;
    display: inline-block;
    top: -20px;
    content: '';
    width: 20px;
    height: 2px;
    background: var(--accent-color);
  }
}

.teacher__education-item:not(:last-child) {
  margin-bottom: 25px;
}

.teacher-link {
  margin-bottom: 50px;

  @include font(500, 12px, 1.67, 0.08);
}

@media screen and (min-width: 768px) {
  .teacher__columns {
    display: flex;
  }

  .teacher__left-side {
    margin-right: 41px;
    margin-bottom: 0;
    min-width: 243px;
  }
  .teacher__main-heading {
    font-size: 26px;
  }
  .teacher__container {
    padding-top: 140px;
  }
  .teacher__achievements-description {
    @include font(600, 14px, 1.22, 0.06);
  }
  .teacher__achievements {
    margin-bottom: 38px;
  }
  .teacher__education::before {
    top: -15px;
  }
  .teacher__achievements-item:not(:last-child) {
    margin-bottom: 35px;
  }
}

@media screen and (min-width: 1366px) {
  .teacher__container {
    padding-top: 162px;
    padding-bottom: 130px;
  }
  .teacher__main-heading {
    font-size: 46px;
    margin-bottom: 50px;
  }
  .teacher__left-side {
    margin-right: 31px;
    width: 530px;
  }
  .teacher__right-side {
    width: 614px;
  }
  .teacher__photo {
    width: 530px;
  }
  .teacher__name {
    padding-bottom: 15px;
    font-size: 30px;
    margin-bottom: 30px;
  }
  .teacher__achievements {
    margin-bottom: 59px;
  }

  .teacher__list-style {
    width: 25px;
    height: 20px;
    margin-right: 35px;
  }
  .teacher__check-container {
    width: 25px;
    height: 20px;
  }
  .teacher__education-description {
    font-size: 18px;
    line-height: 1.56;
  }
  .teacher__education {
    margin-bottom: 20px;

    &::before {
      width: 33px;
      top: -25px;
    }
  }
  .teacher__achievements-description {
    @include font(600, 16px, 1.22, 0.06);
  }
  .teacher-link {
    margin-bottom: 0;
    font-size: 18px;
    line-height: 1.56;
  }

  .teacher__columns {
    margin-bottom: 130px;
  }
  .teacher__achievements-description::before {
    height: 20px;
    position: absolute;
    left: 0px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .teacher__achievements-item {
    padding-left: 60px;
  }
  .teacher__education-item:not(:last-child) {
    margin-bottom: 23px;
  }
}
