.scope {
  padding-bottom: 50px;

  @media screen and (min-width: 1366px) {
    padding-bottom: 130px;
  }
}

.scope__title {
  @include font(800, 20px, 1.3, $ls: 0.03em);
  text-align: center;
  padding-bottom: 30px;
  color: var(--main-color);

  @media screen and (min-width: 768px) {
    @include font(800, 26px, 1.307, $ls: 0.03em);
  }

  @media screen and (min-width: 1366px) {
    @include font(800, 46px, 1.304, $ls: 0.03em);
    padding-bottom: 50px;
    text-align: start;
  }
}

.scope_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media screen and (min-width: 768px) {
    justify-content: space-between;
  }
}

.scope_list__item {
  width: 280px;
  min-height: 280px;
  box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
  background: var(--white-color);

  @media screen and (min-width: 768px) {
    width: 334px;
    max-height: 334px;
  }

  @media screen and (min-width: 1366px) {
    width: 425px;
    max-height: 396px;
  }
}

.scope_list__item:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 15px;
  }
}

.scope_list__item:nth-child(-n + 4) {
  @media screen and (min-width: 768px) and (max-width: 1365px) {
    margin-bottom: 27px;
  }
}

.scope_list__item:nth-child(-n + 3) {
  @media screen and (min-width: 1366px) {
    margin-bottom: 20px;
  }
}

.scope-link {
  height: 280px;
  text-decoration: none;
  display: block;

  padding-top: 30px;
  padding-right: 15px;
  padding-left: 15px;
  transition: background-color 250ms var(--timing);

  @media screen and (min-width: 768px) {
    height: 334px;
    padding-top: 40px;
    padding-right: 20px;
    padding-left: 20px;
  }

  @media screen and (min-width: 1366px) {
    height: 396px;
  }
}

.scope-link:hover,
.scope-link:focus {
  background-color: var(--accent-color);
}

.scope_list__icon {
  fill: var(--accent-color);
  transition: fill 250ms var(--timing);
}

.scope-link:hover .scope_list__icon,
.scope-link:focus .scope_list__icon {
  fill: var(--white-color);
}

.scope-link:hover .scope_list__title::after,
.scope-link:focus .scope_list__title::after {
  background-color: var(--white-color);
}

.scope-link:hover .scope_list__title,
.scope-link:focus .scope_list__title {
  color: var(--white-color);
}

.scope-link:hover .scope_list__description,
.scope-link:focus .scope_list__description {
  color: var(--white-color);
}

.scope_list__box {
  display: flex;
  justify-content: center;
  align-items: center;

  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    padding-bottom: 30px;
  }
}

.scope_list__title {
  @include font(700, 14px, 1.428, $ls: 0.08em);
  text-align: center;
  padding-bottom: 42px;
  position: relative;
  color: var(--main-color);
  max-width: 280px;

  @media screen and (min-width: 768px) {
    max-width: 293px;
    padding-bottom: 52px;
  }

  @media screen and (min-width: 1366px) {
    @include font(700, 20px, 1.4, $ls: 0.08em);
    max-width: 365px;
    padding-bottom: 32px;
  }
}

.scope_list__title::after {
  position: absolute;
  display: inline-block;
  content: '';
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background: var(--accent-color);

  @media screen and (min-width: 768px) {
    bottom: 25px;
  }

  @media screen and (min-width: 1366px) {
    bottom: 15px;
  }
}

.scope_list__description {
  @include font(500, 12px, 1.666, $ls: 0.08em);
  display: block;
  text-align: center;
  color: var(--secondary-color);
  max-width: 280px;

  @media screen and (min-width: 768px) {
    max-width: 307px;
  }

  @media screen and (min-width: 1366px) {
    @include font(500, 18px, 1.555, $ls: 0.08em);
    max-width: 365px;
  }
}
